.statistics {
    width: 180px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 20px;

    border-radius: 4px 4px 4px 4px;
    box-shadow: 0 0 5px #a169b5, 0 0 10px #9d41bf;
  }

  .stat_list {
    display: flex;
  }
  .title {
    text-align: center;
    padding: 15px;

    font-weight: bold;
    font-size: 18px;
    color: #000000;;

    background-color: #4f4f4f;
  }
  
  .item {
    width: 35px;
    display: flex;
    flex-direction: column;
    text-align: center;
    border:  1px solid rgb(37, 13, 45);
  }

.item:nth-child(5n) {
  border-radius: 0 0 4px 0;
}
.item:first-child{
  border-radius: 0 0 0 4px;
}

.label{
  font-size: 12px;
  color: #000000;
}

.percentage{
  font-size: 12px;
  color: #000000;
}
